.wind .reading{-ms-grid-column:2;-ms-grid-column-span:1;grid-column:2 / 3}.wind__direction{--size: 6rem;width:var(--size);height:var(--size);border-radius:50%;background-color:rgba(255,255,255,0.5);display:-ms-grid;display:grid;place-items:center;-ms-grid-row:1;-ms-grid-row-span:2;grid-row:1 / span 2}.wind__arrow{--direction: 180deg;--size: 1rem;height:calc(var(--size) * 3);width:var(--size);background:#613131;background:var(--clr-accent-dark);-webkit-clip-path:polygon(50% 0, 0% 100%, 100% 100%);clip-path:polygon(50% 0, 0% 100%, 100% 100%);transform:translateY(-50%) rotate(var(--direction));transform-origin:bottom center;transition:transform 500ms ease}.info{-ms-grid-column:1;-ms-grid-column-span:2;grid-column:1 / 3}.unit{-ms-grid-column:3;-ms-grid-column-span:1;grid-column:3 / 4;place-self:end;color:#fff;color:var(--clr-light);display:flex;opacity:.7;transition:opacity 275ms linear}.unit:hover{opacity:1}.unit label{cursor:pointer}.unit input{clip:rect(0 0 0 0);-webkit-clip-path:inset(50%);clip-path:inset(50%);height:1px;overflow:hidden;position:absolute;white-space:nowrap;width:1px}.unit__toggle{cursor:pointer;width:4em;border:2px solid #fff;border:2px solid var(--clr-light);background:transparent;padding:0;border-radius:100vmax;margin:0 1em}.unit__toggle::after{content:'';display:block;background:#fff;background:var(--clr-light);border-radius:50%;height:1rem;margin:3px;margin-left:auto;width:1rem}:checked ~ .unit__toggle::after{margin-left:3px}

.footer{background-color:#0c0c0c;-webkit-clip-path:polygon(10% 100%, 90% 100%, 100% 0, 0 0);clip-path:polygon(10% 100%, 90% 100%, 100% 0, 0 0);margin:0 15rem;display:flex;flex-direction:column;justify-content:space-around}.footer__icons{flex-grow:1}.footer__icons li{display:inline-block;padding:1rem}.footer__icon{height:3.5rem;width:3.5rem;fill:black;background-color:#ff0028;padding:1rem;border-radius:30%}.footer__navigation li{font-size:1.5rem;padding-right:1rem;color:#e9e9e9;letter-spacing:1.2px}.footer__navigation li:not(:last-child){border-right:2px solid #727272}.footer__navigation li:hover{color:white}.footer__subnavigation li{font-size:1.3rem;letter-spacing:0.8px;padding-right:0.8rem;color:#c8c8c8}.footer__subnavigation li:not(:last-child){border-right:2px solid #727272}.footer__subnavigation li:hover{color:#fff}.footer__copyright{width:100%;border-top:1px solid #ff0028;margin-top:1rem;align-self:flex-end;padding-top:1rem;font-size:1.5rem;color:#727272}li{display:inline-block;margin:1rem;cursor:pointer}.name{color:#ff0028}

